<template>
  <div id="login">
    <div class="nav">
      <div class="title">
        <van-icon name="cross" size="25" @click="back" />登录
      </div>
    </div>
    <div class="text">
      <van-cell-group>
        <van-field v-model="username" clearable placeholder="请输入用户名" @keyup="getInputValue" />

        <van-field
          v-model="password"
          :right-icon="eye"
          :type="type"
          placeholder="请输入密码"
          @keyup="getInputValue"
          @click-right-icon="getIcon"
        />
      </van-cell-group>

      <p class="pas">
        <a>忘记密码?</a>
      </p>

      <button v-if="btnShow" class="red" @click="login">登录</button>
      <button v-if="!btnShow" class="chushi" @click="login">登录</button>

      <p class="reg">
        <a>短信验证码登录</a>
        <a>注册</a>
      </p>
    </div>
    <footer>
      <div class="top">
        <p>
          <span></span>
        </p>
        <p>使用社交账号登录</p>
        <p>
          <span></span>
        </p>
      </div>
      <div class="bottom">
        <i class="iconFont"></i>
        <i class="iconFont"></i>
        <i class="iconFont"></i>
      </div>
    </footer>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      username: '',
      password: '',
      btnShow: false,
      eye: 'closed-eye',
      type: 'password'
    }
  },
  methods: {
    back: function () {
      this.$router.go(-1)
    },
    getInputValue: function () {
      // eslint-disable-next-line eqeqeq
      if (this.username != '' && this.password != '') {
        this.btnShow = true
      } else {
        this.btnShow = false
      }
    },
    getIcon: function () {
      // eslint-disable-next-line eqeqeq
      if (this.eye == 'closed-eye') {
        this.eye = 'eye-o'
        this.type = 'text'
      } else {
        this.eye = 'closed-eye'
        this.type = 'password'
      }
    },
    login: function () {
      var _this = this
      axios.post('http://10.2.1.27:8081/logIn', {
        username: _this.username,
        password: _this.password
      })
        .then(function (res) {
          console.log(res)
          // eslint-disable-next-line eqeqeq
          if (res.data.state == true) {
            sessionStorage.token = res.data.token
            sessionStorage.username = _this.username
            history.go(0)
          }
        })
        .catch(function (err) {
          console.log(err)
        })
    }
  },
  mounted () {
    // eslint-disable-next-line eqeqeq
    if (sessionStorage.token != undefined) {
      this.$router.replace(
        '/car',
        function (to) {
          // console.log('跳转成功', to)
        },
        function (to) {
          // 跳转失败时的目标路由 to 为undefined
          console.log('跳转失败', to)
        }
      )
    }
  }
}
</script>

<style scoped lang="less">
#login {
  .nav {
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0;
    background: rgb(255, 255, 255);

    .title {
      width: 100%;
      height: 100%;
      text-align: center;
      line-height: 60px;
      font-size: 20px;
      position: relative;

      i {
        position: absolute;
        left: 10px;
        top: 17px;
      }
    }
  }
  .text {
    width: 100%;
    height: 800px;
    position: fixed;
    overflow: hidden;
    margin-top: 60px;
    box-sizing: border-box;
    padding: 20px;
    background-color: #fff;
    display: flex;
    flex-direction: column;

    .van-cell {
      outline: 0 !important;
      border-top: 0 !important;
      border-bottom: 1px solid rgb(177, 176, 176);
    }

    .pas {
      width: 100%;
      height: 30px;
      position: relative;

      a {
        width: 100px;
        height: 100%;
        line-height: 30px;
        font-size: 13px;
        text-align: right;
        color: gray;
        position: absolute;
        right: 0;
      }
    }

    .reg {
      width: 100%;
      height: 30px;
      margin-top: 5px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      a {
        height: 30px;
        line-height: 30px;
        font-size: 13px;
        box-sizing: border-box;
        padding: 0 5px;
        color: gray;
        right: 0;
      }
    }

    .chushi {
      border: 0;
      outline: 0;
      width: 100%;
      height: 40px;
      border-radius: 5px;
      color: rgb(255, 255, 255);
      background: rgb(175, 175, 175);
    }

    .red {
      border: 0;
      outline: 0;
      width: 100%;
      height: 40px;
      border-radius: 5px;
      color: rgb(255, 255, 255);
      background: rgb(179, 6, 6);
    }
  }
  footer {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;

    .top {
      width: 100%;
      height: 30px;
      display: flex;
      flex-direction: row;
      justify-content: space-around;

      p {
        flex: 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: gray;

        span {
          display: inline-block;
          width: 80%;
          height: 1px;
          background: gray;
        }
      }
    }

    .bottom {
      width: 100%;
      height: 70px;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;

      .iconFont {
        width: 40px;
        height: 40px;
        font-style: normal;
      }

      .iconFont:nth-child(1){
        background: url('../../assets/icon/微信.png') no-repeat;
      }
      .iconFont:nth-child(2){
        background: url('../../assets/icon/qq.png') no-repeat;
      }
      .iconFont:nth-child(3){
        background: url('../../assets/icon/微博.png') no-repeat;
      }
    }
  }
}
</style>
