<template>
  <div class="newBooks">
    <!-- 搜索框 -->
    <nav>
      <div class="saoBox">
        <van-icon @click="back" class="sao" name="arrow-left" color="gray" />
      </div>
      <van-search class="serch" input-align="center" placeholder="商品名称 作者 出版社 ISBN"></van-search>
      <div class="saoBox" @click="val1=!val1">
        <van-icon class="sao" name="ellipsis" color="gray" />
      </div>
    </nav>
    <transition>
      <div v-if="val1" class="more">
        <van-icon size="15px" name="chat-o" />
        <span>消息</span>
        <br />
        <van-icon size="15px" name="wap-home" />
        <span>首页</span>
        <br />
        <van-icon size="15px" name="search" />
        <span>搜索</span>
        <br />
        <van-icon size="15px" name="gold-coin" />
        <span>我的竞拍</span>
      </div>
    </transition>

    <!-- 导航栏 -->
    <van-collapse class="navbar" v-model="activeNames">
      <!-- <van-collapse-item>
      </van-collapse-item>-->
      <van-collapse-item class="topbar" title="最新上书" value="展开" name="2">
        <span class="choos choosed" id="all" @click="check1">全部</span>
        <span class="choos" @click="check2">教辅材料</span>
        <span class="choos" @click="check3">文学</span>
        <span class="choos" @click="check1">哲学心理学</span>
        <span @click="check2" class="choos">小说</span>
        <span class="choos" @click="check3">工程技术</span>
        <span class="choos" @click="check1">社会文化</span>
        <span class="choos" @click="check2">艺术</span>
        <span class="choos" @click="check3">管理</span>
        <span class="choos">外文字画</span>
        <span class="choos">名人字画</span>
        <span class="choos">邮票税票</span>
        <span class="choos">期刊</span>
        <span class="choos">工艺品</span>
      </van-collapse-item>
    </van-collapse>
    <div class="tips">
      <!-- <van-tag class="tip" round type="danger" @click="check1">全部</van-tag> -->
      <span class="choos choosed" id="all" @click="check1">全部</span>
      <span class="choos" @click="check2">教辅材料</span>
      <span class="choos" @click="check3">文学</span>
      <span class="choos" @click="check1">哲学心理学</span>
      <span @click="check3" class="choos">小说</span>
      <span class="choos" @click="check2">工程技术</span>
      <span @click="check3" class="choos">社会文化</span>
    </div>
    <!-- 筛选栏 -->
    <van-dropdown-menu class="menu" active-color="red">
      <van-dropdown-item v-model="value" :options="option" />
      <van-dropdown-item title="价格" v-model="value2" :options="option2" />
      <van-dropdown-item title="出版" v-model="value3" :options="option3" />
      <van-dropdown-item title="筛选" ref="item">
        <van-switch-cell v-model="switch1" title="包邮" />
        <van-switch-cell v-model="switch2" title="团购" />
        <van-button block type="info" @click="onConfirm">确认</van-button>
      </van-dropdown-item>
    </van-dropdown-menu>
    <!-- 书单 -->

    <div class="box2">
      <div v-for="(v,i) in lists" :key="i">
        <template>
          <div class="list">
            <div class="new">
              <div class="left"  @click="toPage">
                <img :src="v.img" alt />
              </div>
              <div class="right">
                <h3 class="rtitle"  @click="toPage">{{v.name}}</h3>
                <p class="rtitle"  @click="toPage" style="font-weight:400">{{v.author}}</p>
                <p class="text"  @click="toPage">{{v.address}}</p>
                <div class="shop">
                  <span class="price"  @click="toPage">￥{{v.price}}</span>
                  <van-tag class="tags"  @click="toPage" size="medium" plain>{{v.new}}</van-tag>
                  <van-tag class="tags"  @click="toPage" size="medium" plain>快递6元</van-tag>
                  <van-icon style="z-index:99" @click="toshop(v)" name="cart-o" size="20px" color="rgb(179, 6, 6)" />
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import data1 from '../../json/data.js'
import data2 from '../../json/dataA.js'
import data3 from '../../json/datab.js'
export default {
  name: 'newBooks',
  data () {
    return {
      activeNames: ['1'],
      num: 0,
      car: [],
      val1: false,
      active: 2,
      value: 0,
      value2: 0,
      value3: 0,
      switch1: false,
      lists: [],
      switch2: false,
      option: [
        { text: '全部商品', value: 0 },
        { text: '新款商品', value: 1 },
        { text: '活动商品', value: 2 }
      ],
      option2: [
        { text: '总价从高到低', value: 0 },
        { text: '总价从低到高', value: 1 },
        { text: '价格从低到高', value: 2 },
        { text: '价格从高到低', value: 3 }
      ],
      option3: [{ text: '正序', value: 0 }, { text: '倒序', value: 1 }]
    }
  },
  components: {},
  methods: {
    back: function () {
      this.$router.replace('/')
    },
    onConfirm () {
      this.$refs.item.toggle()
    },
    toshop (v) {
      this.$toast({
        message: '加入购物车成功！',
        icon: 'shopping-cart-o',
        duration: '1000'
      })
      var _this = this
      _this.car.length === 0 ? _this.car.push(v) : _this.car.forEach(function (ele) {
        // eslint-disable-next-line eqeqeq
        if (ele.name != v.name) {
          _this.car.push(v)
        } else {
          ele.id += 1
        }
      })
      this.$store.commit('toCar', _this.car)
    },
    check1 () {
      this.lists = data1
      document.getElementById('all').classList.remove('choosed')
    },
    check2 () {
      this.lists = data2
      document.getElementById('all').classList.remove('choosed')
    },
    check3 () {
      this.lists = data3
      document.getElementById('all').classList.remove('choosed')
    },
    toPage () {
      this.$router.push('newBooks/dPage')
    }
  },
  mounted () {
    this.lists = data2
  }
}
</script>

<style lang="less" scoped>
/* 显示时的动画 */
.v-enter-active {
  animation: an1 0.3s;
}
/* 隐藏时的动画 */
.v-leave-active {
  animation: an2 0.3s;
}
@keyframes an1 {
  0% {
    opacity: 0;
    transform: scale(0.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes an2 {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.1);
  }
}
.more {
  position: fixed;
  top: 50px;
  right: 10px;
  z-index: 999;
  width: 130px;
  height: 150px;
  background: white;
  text-align: left;
  box-sizing: border-box;
  padding-left: 30px;
  padding-top: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px gray;
  span {
    font-weight: 300;
    font-size: 15px;
    position: relative;
    left: 10px;
  }
}
.box2 {
  width: 100%;
  background: white;
  margin-top: 3px;
  padding-top: 20px;
}
.new {
  height: 150px;
  box-sizing: border-box;
  padding: 20px 10px;
  border-bottom: 1px solid rgb(208, 211, 208);
  display: flex;
  .left {
    width: 30%;
    height: 100%;
    position: relative;
    // border: 1px solid pink;
    img {
      width: 100%;
      height: 110px;
      z-index: 1;
    }
  }
  .right {
    width: 70%;
    height: 90%;
    text-align: left;
    font-size: 10px;
    box-sizing: border-box;
    padding: 0 20px;
    h3 {
      position: relative;
      left: 5px;
    }
    .shop {
      width: 100%;
      height: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 10px;
    }
    .price {
      color: rgb(179, 6, 6);
      font-size: 15px;
    }
    .tags {
      height: 15px;
      // padding: 0;
      padding-top: 0 !important;
      font-size: 10px !important;
    }

    .rtitle {
      font-size: 14px;
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
    p {
      color: rgb(73, 67, 67);
      font-size: 12px;
      text-align: left;
      box-sizing: border-box;
      padding: 0 5px;
      margin-top: 10px;
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
    .tips {
      font-weight: 100;
      color: rgb(87, 87, 1);
      padding: 1px 5px;
      border: 1px solid rgb(150, 150, 3);
      border-bottom-right-radius: 10px;
      position: relative;
      top: 20px;
      left: -50px;
      margin-left: 5px;
    }
  }
}
.menu {
  z-index: 99 !important;
}
.topbar {
  height: 50px;
  text-align: left;
  font-weight: 400;
  z-index: 999 !important;
  background: white;
}
.tips {
  width: 100%;
  height: 80px;
  margin-top: -10px;
  background: white;
  box-sizing: border-box;
  padding: 5px 20px;
  line-height: 20px;
  z-index: 1;
  // border: 1px solid pink;
  .tip {
    margin-left: 10px;
  }
}
.choos {
  font-size: 15px;
  border-radius: 5px;
  padding: 2px 5px;
  margin-left: 20px;
  line-height: 40px;
}
.choos:hover {
  background: rgb(179, 6, 6);
  color: white;
}
.choosed {
  background: rgb(179, 6, 6);
  color: white;
}
.navbar {
  margin-top: 50px;
  z-index: 990;
}

.serch {
  width: 80%;
  height: 50px;
  margin-top: -5px;
}
.saoBox {
  display: flex;
  align-items: center;
}
nav {
  width: 100%;
  height: 60px;
  background: white;
  border: 5px solid white;
  display: flex;
  margin-bottom: 15px;
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  padding: 3px 5px;
  z-index: 999;
}
</style>
