<template>
  <div id="car">
    <div class="nav">
      <div class="title">
        <van-icon name="chat-o" size="25" />购物车
      </div>
    </div>

    <template v-if="this.$store.state.book.toCar == ''">
      <div class="car">
        <van-icon name="shopping-cart-o" size="130" />
        <p>购物车是空的</p>
        <button @click="toNewBooks">去逛逛</button>
      </div>
      <div class="recommend">
        <div class="title">
          <div>
            <span></span>
          </div>
          <div>
            <van-icon name="like-o" size="20" />&nbsp;为你推荐
          </div>
          <div>
            <span></span>
          </div>
        </div>
      </div>
      <div class="img">
        <div class="imgLeft" v-if="lists.length > 0">
          <template v-for="(v,i) in lists[0].left">
            <div :key="i">
              <img :src="v.imgUrl" />
              <p>{{v.bookName}}</p>
              <p>
                <span>{{v.xxio}}</span>
                <span>{{v.site}}</span>
              </p>
              <div class="pri">{{v.price}}</div>
            </div>
          </template>
        </div>
        <div class="imgRight" v-if="lists.length > 0">
          <template v-for="(v,i) in lists[0].right">
            <div :key="i">
              <img :src="v.imgUrl" />
              <p>{{v.bookName}}</p>
              <p>
                <span>{{v.xxio}}</span>
                <span>{{v.site}}</span>
              </p>
              <div class="pri">{{v.price}}</div>
            </div>
          </template>
        </div>
      </div>
    </template>
    <template v-if="this.$store.state.book.toCar != ''">
      <div class="carList">
        <template v-for="(v,i) in toshop">
          <div class="list" :key="i">
            <div class="title">
              <div class="icon">
                <van-checkbox-group v-model="result">
                  <van-checkbox v-model="list" :name="v" value="1" checked-color="rgb(179, 6, 6)"></van-checkbox>
                </van-checkbox-group>
              </div>
              <p>
                <van-icon name="shop-o" size="25" />
                &nbsp;{{v.address}}
              </p>
            </div>
            <div class="shop">
              <div class="icon">
                <van-checkbox-group v-model="result">
                  <van-checkbox v-model="list" :name="v" value="2" checked-color="rgb(179, 6, 6)"></van-checkbox>
                </van-checkbox-group>
              </div>
              <img :src="v.img" />
              <div class="particulars">
                <div class="title">{{v.name}}</div>
                <p>{{v.new}}</p>
                <div class="pri">
                  <span>￥{{v.price}}</span>
                  <span>数量:{{v.id}}</span>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div class="carFoot">
        <div class="icon">
            <van-checkbox v-model="checked" @change="onChange" checked-color="rgb(179, 6, 6)"></van-checkbox>
        </div>
        <div class="reduce">
          <span>不含运费</span>
          <p>
            <label style="color: black;font-size: 15px;">合计:</label>{{toPrices}}
          </p>
          <button>结算(0)</button>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
// @ is an alias to /src
import carTui from '../json/carTui'
export default {
  name: 'car',
  data () {
    return {
      lists: [],
      list: [],
      result: [],
      car: [],
      checked: false,
      number: 1
    }
  },
  methods: {
    toNewBooks () {
      this.$router.replace('/newBooks')
    },
    onChange () {
      if (this.checked === false) {
        this.result = []
      } else {
        if (this.result.length !== this.toshop.length) {
          this.result = this.toshop
        }
      }
    },
    select: function (v) {
      this.icon = ''
      console.log(v.price, v.id)
    },
    unique (arr) {
      let unique = {}
      arr.forEach(function (item) {
        unique[JSON.stringify(item)] = item // 键名不会重复
        // console.log(item)
      })
      arr = Object.keys(unique).map(function (u) {
        // Object.keys()返回对象的所有键值组成的数组，map方法是一个遍历方法，返回遍历结果组成的数组.将unique对象的键名还原成对象数组
        // console.log(u)
        return JSON.parse(u)
      })
      return arr
    }
  },
  computed: {
    toshop () {
      return this.unique(this.car)
    },
    toPrices () {
      return this.result.reduce((sum, book) => {
        return sum + book.price * book.id
      }, 0)
    }
  },
  mounted () {
    this.lists = carTui
    this.car = this.$store.state.book.toCar

    if (sessionStorage.token === undefined) {
      this.$router.replace(
        '/login',
        function (to) {
          // console.log('跳转成功', to)
        },
        function (to) {
          // 跳转失败时的目标路由 to 为undefined
          console.log('跳转失败', to)
        }
      )
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../css/car.less");
</style>
