import Vue from 'vue'
import Router from 'vue-router'

import { home, homes } from './router/home'
import { book, books } from './router/books'
import { auction, auctions } from './router/auction'
import { car, cars } from './router/car'
import { about, abouts } from './router/about'
import Main from './views/Main.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      components: {
        default: Main
      },
      children: [
        ...home,
        ...book,
        ...auction,
        ...car,
        ...about

      ]
    },
    ...homes,
    ...books,
    ...auctions,
    ...cars,
    ...abouts
  ]
})
