import Home from '../views/Home.vue'
import newBooks from '../components/par/newBooks.vue'
import dPage from '../components/par/detailsPage.vue'

var home = [
  {
    path: '/',
    components:
     {
       default: Home
     }
    // children: [
    //   {
    //     path: '/',
    //     component: Home
    //   }
    // ]
  }
]

var homes = [
  {
    path: '/newBooks',
    name: 'newBooks',
    component: newBooks
  },
  {
    path: '/newBooks/dPage',
    component: dPage
  }
]

export { home, homes }
